'use client'

import { useState, useEffect } from 'react'
import { Layout } from '@/components/Layout'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import Link from 'next/link'

interface Form {
  id: string
  title: string
  description: string
  type: 'static' | 'dynamic'
  createdAt: string
  isShared?: boolean
  sharedBy?: string
}

export default function HomePage() {
  const [myForms, setMyForms] = useState<Form[]>([])
  const [sharedForms, setSharedForms] = useState<Form[]>([])
  const [isAuthenticated, setIsAuthenticated] = useState(false)

  useEffect(() => {
    // Mock authentication check
    const mockAuth = localStorage.getItem('mockAuth')
    setIsAuthenticated(!!mockAuth)

    // Mock forms data
    if (mockAuth) {
      setMyForms([
        {
          id: '1',
          title: 'Bug Report Form',
          description: 'Standard bug reporting form for development team',
          type: 'static',
          createdAt: '2024-01-15'
        },
        {
          id: '2',
          title: 'Feature Request Form',
          description: 'Custom form for collecting feature requests',
          type: 'dynamic',
          createdAt: '2024-01-10'
        }
      ])

      setSharedForms([
        {
          id: '3',
          title: 'User Feedback Form',
          description: 'Collect user feedback and suggestions',
          type: 'static',
          createdAt: '2024-01-12',
          isShared: true,
          sharedBy: 'john.doe@company.com'
        }
      ])
    }
  }, [])

  if (!isAuthenticated) {
    return (
      <Layout>
        <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-background to-muted/20">
          <Card className="w-full max-w-md">
            <CardHeader className="text-center">
              <CardTitle className="text-2xl font-bold">Welcome to FormLyne</CardTitle>
              <CardDescription>
                Create and manage forms that integrate with Linear issue tracking
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <Button 
                className="w-full" 
                onClick={() => {
                  localStorage.setItem('mockAuth', 'true')
                  setIsAuthenticated(true)
                }}
              >
                Sign in with Google
              </Button>
              <div className="text-center text-sm text-muted-foreground">
                Mock authentication for development
              </div>
            </CardContent>
          </Card>
        </div>
      </Layout>
    )
  }

  return (
    <Layout>
      <div className="container mx-auto px-4 py-8 space-y-8">
        {/* Header */}
        <div className="space-y-2">
          <h1 className="text-3xl font-bold tracking-tight">Dashboard</h1>
          <p className="text-muted-foreground">
            Manage your forms and create new ones to integrate with Linear
          </p>
        </div>

        {/* Quick Actions */}
        <div className="flex flex-col sm:flex-row gap-4">
          <Link href="/forms/static">
            <Button size="lg" className="w-full sm:w-auto">
              Create Static Form
            </Button>
          </Link>
          <Link href="/forms/dynamic">
            <Button variant="outline" size="lg" className="w-full sm:w-auto">
              Create Dynamic Form
            </Button>
          </Link>
        </div>

        <Separator />

        {/* My Workspace */}
        <section className="space-y-4">
          <div className="flex items-center justify-between">
            <h2 className="text-2xl font-semibold">My Workspace</h2>
            <Badge variant="secondary">{myForms.length} forms</Badge>
          </div>
          
          {myForms.length === 0 ? (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12">
                <div className="text-center space-y-2">
                  <h3 className="text-lg font-medium">No forms yet</h3>
                  <p className="text-muted-foreground">
                    Create your first form to get started
                  </p>
                </div>
              </CardContent>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {myForms.map((form) => (
                <Card key={form.id} className="hover:shadow-md transition-shadow">
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <CardTitle className="text-lg">{form.title}</CardTitle>
                      <Badge variant={form.type === 'static' ? 'default' : 'secondary'}>
                        {form.type}
                      </Badge>
                    </div>
                    <CardDescription>{form.description}</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="flex items-center justify-between">
                      <span className="text-sm text-muted-foreground">
                        Created {form.createdAt}
                      </span>
                      <div className="flex gap-2">
                        <Button variant="ghost" size="sm">
                          Edit
                        </Button>
                        <Button variant="ghost" size="sm">
                          Share
                        </Button>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </section>

        <Separator />

        {/* Shared Forms */}
        <section className="space-y-4">
          <div className="flex items-center justify-between">
            <h2 className="text-2xl font-semibold">Shared Forms</h2>
            <Badge variant="secondary">{sharedForms.length} forms</Badge>
          </div>
          
          {sharedForms.length === 0 ? (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12">
                <div className="text-center space-y-2">
                  <h3 className="text-lg font-medium">No shared forms</h3>
                  <p className="text-muted-foreground">
                    Forms shared by others will appear here
                  </p>
                </div>
              </CardContent>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {sharedForms.map((form) => (
                <Card key={form.id} className="hover:shadow-md transition-shadow">
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <CardTitle className="text-lg">{form.title}</CardTitle>
                      <Badge variant={form.type === 'static' ? 'default' : 'secondary'}>
                        {form.type}
                      </Badge>
                    </div>
                    <CardDescription>{form.description}</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-2">
                      <div className="text-sm text-muted-foreground">
                        Shared by {form.sharedBy}
                      </div>
                      <div className="flex items-center justify-between">
                        <span className="text-sm text-muted-foreground">
                          Created {form.createdAt}
                        </span>
                        <Button variant="ghost" size="sm">
                          Use Form
                        </Button>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </section>
      </div>
    </Layout>
  )
}
